import React, { useState, useEffect } from "react";
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableRow from '@material-ui/core/TableRow';
import ListItemText from '@material-ui/core/ListItemText';
import TextField from '@material-ui/core/TextField';
import Paper from '@material-ui/core/Paper';
import Container from '@material-ui/core/Container';
import Button from '@material-ui/core/Button';
import Loader from "./loader";
import { db } from "../firebase";
import { ref, onValue } from "firebase/database";

const useStyles = makeStyles((theme) => ({
    table: {
        width: '100%',
    },
    main: {
        marginTop: '70px',
    },
    text: {
        width: '100%',
        marginBottom: '1em'
    }
}));

const Drivers = () => {
    const [drivers, setDrivers] = useState([]);
    const [find, setFind] = useState("");
    const [loading, setLoading] = useState(false);
    const classes = useStyles();

    useEffect(() => {
        setLoading(true);
        onValue(ref(db, `/drivers/`), (snapshot) => {
            setDrivers([]);
            const data = snapshot.val();
            if (data !== null) {
                Object.values(data).map((driver) => (
                    setDrivers((oldArray) => [...oldArray, driver])
                ));
                setLoading(false);
            }
        });
    }, []);

    const driversSort = (arr) => {
        arr.sort((a, b) => (a.nick > b.nick ? 1 : -1))
        return arr;
      };

    const filteredDrivers = drivers.filter((driver) => {
        return driver.name.toLowerCase().includes(find.toLocaleLowerCase()) + driver.nick.toLowerCase().includes(find.toLocaleLowerCase())
    });

    return (
        <>
        {loading ? <Loader/> : 
        <Container className={classes.main}>
        <TextField className={classes.text} id="outlined-basic" label="Поиск..." variant="outlined" onChange={(e) => setFind(e.target.value)} />
        <TableContainer component={Paper}>
            <Table className={classes.table} aria-label="a dense table">
                <TableBody>
                    {driversSort(filteredDrivers).map((item) => (
                        <TableRow key={item.name}>
                            <TableCell component="th" scope="row">
                                <ListItemText primary={item.nick} secondary={item.name} />
                            </TableCell>
                            <TableCell align="right">
                                <Button variant="contained" color="secondary" href={`tel:${item.tel}`}>
                                    {item.tel}
                                </Button>
                            </TableCell>
                        </TableRow>
                    ))}
                </TableBody>
            </Table>
        </TableContainer>
    </Container>
        }
        
        </>
    );
}


export default Drivers;